.App {
  width: 100%;

}

.App-header {
  background-color: #222;
  height: 200px; 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.App-header>.menu-background{
  -webkit-animation: App-header-background 20s linear 5s infinite;
          animation: App-header-background 20s linear 5s infinite;
  -ms-flex-order: 0;
      order: 0;
  width:15vw;
  
}
.App-header>img{
  -ms-flex-positive: .9;
      flex-grow: .9;
  -ms-flex-order: 1;
      order: 1;
  width: 85vw;
  -o-object-fit: cover;
     object-fit: cover;

}



@-webkit-keyframes App-header-background {
  0%{background-color:  #99ebff}
  50%{background-color:black;}
  100%{background-color:  #99ebff}
}



@keyframes App-header-background {
  0%{background-color:  #99ebff}
  50%{background-color:black;}
  100%{background-color:  #99ebff}
}


